.home-container {
  max-width: 1200px;
  margin: 0 auto;
}

.animals-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 20px;
}

.animal-card {
  padding: 16px;
  cursor: pointer;
  transition: transform 0.2s;
}

.animal-card:hover {
  transform: scale(1.05);
}

.animal-card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 8px;
} 